html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

input {
  appearance: none;
}

input::-ms-clear {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration: underline;
}

a:not([class]):hover {
  text-decoration: none;
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 400;
  src: local("SFDisplay Regular"), local("SFDisplay-Regular"), url("../fonts/sfuidisplay-regular.woff2") format("woff2"), url("../fonts/sfuidisplay-regular.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 500;
  src: local("SFDisplay Medium"), local("SFDisplay-Medium"), url("../fonts/sfuidisplay-medium.woff2") format("woff2"), url("../fonts/sfuidisplay-medium.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 600;
  src: local("SFDisplay Semibold"), local("SFDisplay-Semibold"), url("../fonts/sfuidisplay-semibold.woff2") format("woff2"), url("../fonts/sfuidisplay-semibold.woff") format("woff");
}

@font-face {
  font-family: 'SFDisplay';
  font-style: normal;
  font-weight: 700;
  src: local("SFDisplay Bold"), local("SFDisplay-Bold"), url("../fonts/sfuidisplay-bold.woff2") format("woff2"), url("../fonts/sfuidisplay-bold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

* {
  font-family: 'SFDisplay', Arial, sans-serif;
}

body {
  color: #4A4A4A;
  font-family: 'SFDisplay', Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 12px 40px;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.logo {
  float: left;
  padding: 7px 0 6px;
  color: #4A4A4A;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.1875;
}

.logo__in {
  color: inherit;
}

.profile {
  float: right;
  color: #007AFF;
  font-size: 0;
  white-space: nowrap;
}

.profile__img, .profile__title {
  display: inline-block;
  vertical-align: middle;
}

.profile__img {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.profile__title {
  padding: 8px 0 8px 14px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}
