//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight) {
	font-family: 'SFDisplay';
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('SFDisplay Regular', 'SFDisplay-Regular', sfuidisplay-regular); }
@font-face {
	@include fontdefault(normal, 500);
	@include fontpath('SFDisplay Medium', 'SFDisplay-Medium', sfuidisplay-medium); }
@font-face {
	@include fontdefault(normal, 600);
	@include fontpath('SFDisplay Semibold', 'SFDisplay-Semibold', sfuidisplay-semibold); }
@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('SFDisplay Bold', 'SFDisplay-Bold', sfuidisplay-bold); }
