.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	padding: 12px 40px;
	&__inner {
		@include clr; } }

.logo {
	float: left;
	padding: 7px 0 6px;
	color: $darkGray;
	@include f(16px, 700, 1.1875);
	&__in {
		color: inherit; } }

.profile {
	float: right;
	color: $blue;
	font-size: 0;
	white-space: nowrap;
	&__img,
	&__title {
		display: inline-block;
		vertical-align: middle; }
	&__img {
		border-radius: 50%;
		width: 32px;
		height: 32px;
		@include cover; }
	&__title {
		padding: 8px 0 8px 14px;
		@include f(14px, 400, 16px); } }
